angular.module("app").component("home", {
  template: require('./home.html'),
  bindings: {
    userSessions: "=",
    userTasks: "="
  },
  controller: function(
    currentIdentity,
    sessions,
    toastr,
    unreviewedSessionCount
  ) {
    this.currentUser = currentIdentity.currentUser;

    this.setNextSessionToReview = function() {
      sessions
        .getNextUnreviewedSession(currentIdentity.currentUser.id)
        .then(response => {
          this.currentSessionToReview = response.data;
        });
    };
    this.setNextSessionToReview();

    this.voteYes = function() {
      sessions
        .incrementVote(this.currentSessionToReview.id)
        .then(() => {
          return sessions.addReviewedSession(
            this.currentUser.id,
            this.currentSessionToReview.id
          );
        })
        .then(
          function() {
            this.setNextSessionToReview();

            // pull updated value
            unreviewedSessionCount.updateUnreviewedSessionCount();
          }.bind(this)
        );
    };

    this.voteNo = function() {
      sessions
        .addReviewedSession(this.currentUser.id, this.currentSessionToReview.id)
        .then(
          function() {
            this.setNextSessionToReview();

            // pull updated value
            unreviewedSessionCount.updateUnreviewedSessionCount();
          }.bind(this)
        );
    };
  }
});



